@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0;box-sizing:border-box;font-family:"Poppins",Arial,Helvetica,sans-serif}.App{min-height:100%;color:rgba(0,0,0,.85)}h1{color:rgba(0,0,0,.85)}a{text-decoration:none;color:inherit}a:hover{text-decoration:underline;font-weight:bold}.flex{display:flex;align-items:center}.flex-col{display:flex;flex-direction:column}.wrap{flex-wrap:wrap}.space-b{justify-content:space-between}.bold{font-weight:bolder}.full-width{width:100%}.full-height{height:100%}.navbar,footer{min-height:8vh;background-color:rgba(0,0,0,.85);color:#fff;padding:0 10rem}footer{justify-content:center}.navbar .brand-name{font-weight:bold}.navbar .links a{margin-left:20px}@media(min-width: 650px){section{padding:3rem 10rem}}@media(max-width: 650px){section{padding:3rem 2rem}.navbar,footer{padding:0 2rem}}
.btn{padding:10px 30px;background-color:#1978f0;border:2px solid #1978f0;color:#fff;font-weight:bold;border-radius:2px;outline:none;transition:padding 400ms ease-in-out;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.btn:hover{padding:10px 50px;background-color:#166ef0;border:2px solid #166ef0;cursor:pointer;font-weight:bold}.btn:active{background-color:#fff;border:2px solid #166ef0;border-radius:2px;color:#166ef0}@media(max-width: 650px){.btn{font-size:1.1rem}}
section:nth-of-type(even){background-color:#f9f9f9}section:not(:first-of-type) h1{border-bottom:2px solid rgba(0,0,0,.85);margin-bottom:2rem;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;font-size:2rem}.greetings-section{min-height:92vh;background-repeat:no-repeat;background-size:cover}.greetings-section .greetings{margin:auto 0;color:rgba(0,0,0,.85)}.greetings-section .greetings h1,.greetings-section .greetings h2{margin-bottom:2rem}@media(min-width: 650px){.hello{font-size:3rem}.greetings-section{background-position:right}}@media(max-width: 650px){.hello{font-size:2.5rem}.greetings-section{background-position-x:-150px;background-position-y:150px}}.stack{flex-wrap:wrap}.stack img{height:100px;box-shadow:0 0 5px #ccc;padding:10px;border-radius:1rem;margin:2rem 0}.grid-container{display:grid;grid-gap:2rem;grid-template-columns:repeat(auto-fit, minmax(300px, 1fr))}.project-item{box-shadow:0 0 6px #ccc;border-radius:5px}.project-item .img{width:100%;border-radius:5px 5px 0 0}.project-item .description{margin-top:1rem;text-align:center}.project-item .stack{margin-top:1rem;font-weight:lighter;text-align:center}.card-bottom{padding:1rem;align-items:center}.contact-options{align-items:flex-start}.contact-informations{min-width:-webkit-fit-content;min-width:-moz-fit-content;min-width:fit-content;width:50%}.contact-informations .info{display:flex;flex-direction:column;align-items:center;text-align:center}.contact-informations h3{margin-top:1rem}.contact-informations a{color:#1978f0;margin:5px;transition:transform .8s ease-in-out}.contact-informations a:hover{color:#166ef0;transform:rotateZ(360deg)}.contact-form{border-radius:2px;width:40%}.contact-form input,.contact-form textarea{padding:10px;border:1px solid #ccc;border-radius:2px;min-width:300px;margin-bottom:1rem}@media(max-width: 1000px){.contact-options{flex-direction:column}.contact-form{margin-bottom:2rem;width:100%;text-align:center}.contact-form button{margin:auto}.contact-informations{width:100%}}
